<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth, createUrl, formatDate } from "../utils/utils.js"
export default {
  name: 'SettlementFlow',
  data() {
    /**
   * 公共验证函数(验证数字值)
   * @params message - 提示信息
   */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    return {
	approveDialogVisible:false,
	appRoverData:[],//审批人
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      tableFields: { //导出Excel表格的表头设置
        // "机器人编号": "robotCode",
        // "机器人型号": "robotModel",
		"审批流名称":'flowPathName',
        "景区名称": "SCENIC_SPOT_NAME",
		"添加审批人":'addPrev',

        // "故障单号": "faultNumber",
        // "是否影响使用": "ifEffect",
        // "故障状态": "faultState",
        // "故障类型": "faultType",
        // "故障描述": "faultDescribe",
        // "故障图片": "faultImg",
        // "是否需要发配件": "ifNeedAccessory",
        // "收件地址": "receiveAddress",
        // "配件是否已发送": "accessoryIfSend",
        // "配件是否收到": "accessoryIfReceive",
        // "故障上报时间": "faultRecordTime",
        // "上报人员": "recorder",
        // "上报电话": "recordPhone",
        // "关联订单": "relateOrder",
        // "是否质保": "warrantyState",
